



























































import Vue from 'vue';
import {mapGetters} from 'vuex';
import Navigation from '@/components/Navigation.vue';

export default Vue.extend({
  name: 'Workout',
  computed: {
    ...mapGetters({
      workout: 'getActiveWorkout'
    })
  },
  methods: {
    addExercise() {
      this.$router.replace({ name: 'workout_exercises' });
    },
    finishWorkout() {
      this.$store.commit('finishWorkout');
      this.$router.replace('/');
    }
  },
  components: {
    Navigation
  }
});
